import React from 'react';
import ReactDOM from 'react-dom';
import App from './containers/App';
import * as serviceWorker from './serviceWorker';
import { BrowserRouter} from 'react-router-dom';
import homeDesignReducer from './store/reducers/homeDesigns';
import popularPackageReducer from './store/reducers/popularPackageReducer';
import homeDesignPackageContainerReducer from './store/reducers/packageContainerReducer';
import equipmentPackagesReducer from './store/reducers/equipmentPackagesReducer';
import { Provider } from 'react-redux';
import { createStore, combineReducers, applyMiddleware, compose } from 'redux';
import { reduxFirestore, getFirestore, firestoreReducer} from 'redux-firestore';
import firebaseConfig from './config/config';
import thunk from 'redux-thunk';

const rootReducer = combineReducers({
  HDR: homeDesignReducer,
  fsreducer: firestoreReducer,
  PPState: popularPackageReducer,
  HDC: homeDesignPackageContainerReducer,
  EDP: equipmentPackagesReducer
});


const store = createStore(rootReducer, 
  compose(
    applyMiddleware(thunk.withExtraArgument({ getFirestore })),
    reduxFirestore(firebaseConfig)
  )
);

const app = (
  // <Provider store={store}><ReactReduxFirebaseProvider {...rrfProps}><BrowserRouter><App /></BrowserRouter></ReactReduxFirebaseProvider></Provider>
  <Provider store={store}><BrowserRouter><App /></BrowserRouter></Provider>
);
ReactDOM.render(app, document.getElementById('root'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: http://bit.ly/CRA-PWA
serviceWorker.unregister();